import {FormControl} from '@angular/forms';
import {cities} from './cities';
import {Observable} from 'rxjs';
import {DiacriticRegex} from './diacritic-regex';
import {map, startWith} from 'rxjs/operators';

export class CitiesHelper {
  cityCtrl: any;
  private regex: DiacriticRegex;
  private readonly cities: string[][];

  constructor(cityCtrl: FormControl) {
    this.cities = cities;
    this.cities = this.cities.sort((a, b) => {
      if (a[1] < b[1]) {
        return -1;
      }

      if (a[1] > b[1]) {
        return 1;
      }
      return 0;
    });
    this.cityCtrl = cityCtrl;
    this.regex = new DiacriticRegex();
  }

  static isValidCity(city: string): boolean {
    return cities.map(item => item[1]).indexOf(city) !== -1;
  }

  getFilteredCitiesObservable(): Observable<string> {
    // TODO: crear directiva
    return this.cityCtrl.valueChanges.pipe(
      startWith(null),
      map((name: string) => this.filterCities(name))
    );

  }

  private filterCities(val: string) {
    return val ? this.cities.filter(c => this.regex.toRegex({flags: 'gi'})(val).test(c[1]))
      : this.cities;
  }
}
