import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {Reference} from './reference';
import {FormControl, NgForm} from '@angular/forms';
import {CitiesHelper} from './cities-helper';
import {Address} from './address';

@Component({
  selector: 'lib-search-form',
  templateUrl: './search-form.component.html',
  styleUrls: ['./search-form.component.scss'],
})
export class SearchFormComponent implements OnInit {
  address: Address = // TODO: create interface
    {city: 'Managua', reference: '', segments: [{name: ''}], type: null};
  ref: Reference = null;
  cityCtrl: FormControl;
  filteredCities: any;
  @ViewChild('searchForm') searchForm: NgForm;
  copyAddress: string;

  @Input() submitText = 'Buscar';
  @Input() autoClear = true;
  @Output() submitted = new EventEmitter<Address>();

  constructor() {
    this.cityCtrl = new FormControl();
    const citiesHelper = new CitiesHelper(this.cityCtrl);
    this.filteredCities = citiesHelper.getFilteredCitiesObservable();
  }

  ngOnInit(): void {
  }

  addSegment(index: number = null): void {
    if (index === null || index === this.address.segments.length - 1) {
      (this.address.segments as {name: string}[]).push({name: ''});
    }
    return;
  }

  deleteSegment(): void {
    this.address.segments.splice(this.address.segments.length - 1, 1);
  }

  clear(): void {
    this.address = {city: this.address.city, reference: '', segments: [{name: ''}], type: null};
    this.ref = null;
  }

  search() {
    if (!!this.searchForm && this.searchForm.valid && this.cityCtrl.valid && !this.isSameAddress()) {
      const address = {...this.address};
      let localSegments = [...address.segments] as {name: string}[];
      console.log(localSegments);

      if (localSegments.length > 1) {
        localSegments = localSegments
          .filter((el: {name: string}) => !!el && !!el.name);
      }
      localSegments = localSegments
        .map((el: {name: string}) => el.name.trim())
        .join(',')
        .split(',')
        .map(el => ({name: el.trim()}));
      address.segments = localSegments;
      address.fullAddress = this.addressToString(address, true);
      this.submitted.emit(address);
      this.copyAddress = address.fullAddress;
      if (this.autoClear) {
        this.clear();
      }
    }
  }

  isSameAddress(): boolean {
    return this.addressToString(this.address) === this.copyAddress;
  }

  addressToString(address: Address, coloquial = false): string {
    if (coloquial) {
      const segments = !!address.segments ? (address.segments as {name: string}[]).filter(i => !!i.name.trim().length) : [];
      return `${address.reference.trim()}${segments.length ? ', ' + segments.map(i => i.name).join(', ') : ''}. ${address.city}`;
    }

    return `${address.city}${address.reference}${(address.segments as {name: string}[]).map(i => i.name).join('')}`;
  }
}
