import {InjectionToken, NgModule} from '@angular/core';
import { SearchFormComponent } from './search-form.component';
import {MatFormFieldModule} from '@angular/material/form-field';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {MatAutocompleteModule} from '@angular/material/autocomplete';
import {MatIconModule} from '@angular/material/icon';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {CommonModule} from '@angular/common';
import {MatInputModule} from '@angular/material/input';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatButtonModule} from '@angular/material/button';
import { SearchInputComponent } from './search-input/search-input.component';
import {AuthInterceptor} from './AuthInterceptor';

export const BASE_URL = new InjectionToken<string>('BASE_URL');
export const TOKEN = new InjectionToken<string>('TOKEN');

@NgModule({
  declarations: [SearchFormComponent, SearchInputComponent],
  imports: [
    CommonModule,
    BrowserAnimationsModule,
    HttpClientModule,
    MatFormFieldModule,
    ReactiveFormsModule,
    MatAutocompleteModule,
    MatIconModule,
    FormsModule,
    MatInputModule,
    MatButtonModule
  ],
  providers: [
    {provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true, deps: [TOKEN]}
  ],
  exports: [SearchFormComponent, SearchInputComponent]
})
export class AmoxtlatiloyanModule {
  static forRoot(host: string, apiToken: string) {
    return {
      ngModule: AmoxtlatiloyanModule,
      providers: [
        {
          provide: BASE_URL,
          useValue: host
        },
        {
          provide: TOKEN,
          useValue: apiToken
        }
      ]
    };
  }
}
