import {Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild} from '@angular/core';
import {MatFormField} from '@angular/material/form-field';
import {MatInput} from '@angular/material/input';
import {FormControl} from '@angular/forms';

@Component({
  selector: 'lib-search-input',
  templateUrl: './search-input.component.html',
  styleUrls: ['./search-input.component.scss']
})
export class SearchInputComponent implements OnInit, OnChanges {
  address: string;
  @Input() fullAddress: string;
  @Input() hint: boolean | string = false;
  @Input() error: string;
  @Output() submitted: EventEmitter<string> = new EventEmitter<string>();
  addressField = new FormControl();

  constructor() { }

  ngOnInit(): void {
    if (this.fullAddress) {
      this.address = this.fullAddress; // TODO: fix: textarea not resizing when adding initial value
    }
  }

  reset() {
    this.address = '';
  }

  propagate() {
    this.addressField.markAsTouched();
    this.submitted.emit(this.address);
  }

  preventEnter($event: any) {
    $event.preventDefault();
  }

  isString(hint: boolean | string) {
    return typeof hint === 'string';
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['error'] && this.error) {
      this.addressField.setErrors({anyError: true});
    }
  }
}
