class PosModifier {
    name: string;
    pos: google.maps.LatLng | google.maps.LatLngLiteral;
}

export class Reference {
    id: string;
    name: string;
    pos: google.maps.LatLng | google.maps.LatLngLiteral;
    snappedPos: google.maps.LatLng;
    city: string;
    addedBy: string;
    revised: boolean;
    alias: Array<string>;
    posModifiers: Array<PosModifier>;
    accuracy?: number;
    constructor() {
        return {id: null, name: null, pos: {lat: null, lng: null}, city: '', alias: [''], posModifiers: []} as Reference;
    }
}
